import React, { Component } from 'react';
import compass_rose from './media/compass_rose.svg';
import ReactSVG from 'react-svg';


class SteeringCompass extends Component {
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            rose: 0,

        };
        this.displayed = {
            rose: -1,
            cts: -1,
            heading: -1,
        }
    }

    updateSVGCompass(){
        let {heading, cts} = this.props;
        let rose = this.state.rose;
        if (heading !== this.displayed.heading) {
            // $('#headingDisplay').val(this.heading);
            const heading_svg = document.getElementById('compass_heading');
            if (heading_svg) {
                console.log(heading_svg);
                heading_svg.setAttribute("transform", "rotate(" + heading + ")");
                heading_svg.setAttribute("transform-origin", "50% 50%");
                this.displayed.heading = heading
                }
        }
        if (cts !== this.displayed.cts){
            const heading_course = document.getElementById('compass_course');
            if(heading_course){
                console.log(heading_course);
                heading_course.setAttribute("transform", "rotate(" + cts  + ")");
                heading_course.setAttribute("transform-origin", "50% 50%");
                this.displayed.cts = cts
            }
        }
        if (rose !== this.displayed.rose){
            const heading_rose = document.getElementById('compass_rose');
            if(heading_rose) {
                console.log(heading_rose);
                heading_rose.setAttribute("transform", "rotate(" + rose + ")");
                heading_rose.setAttribute("transform-origin", "50% 50%");
                this.displayed.rose = rose
            }
        }
    }

    render() {
        this.updateSVGCompass();
    return (
        <div className="App-Compass">
            <ReactSVG src={compass_rose} />
        </div>

    );
  }
}

export default SteeringCompass;
