import React, { Component } from 'react';
import Slider from '@material-ui/lab/Slider';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Switch from '@material-ui/core/Switch';



class Simulator extends Component {
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            isLoaded: false,
            updated: false,
            on: 0,
            gain: 0,
            speed: 0,
            power_bias: 0,
            slider_gain: 0,
            slider_speed: 0,
            slider_power_bias: 0,
            checkedSimulator: 0,
        };

        this.url = process.env.REACT_APP_THIS_HOST + ":8079/api/simulation";
        this.can_update = true;
        this.gain_scale = 1;
        this.speed_scale = 10;
        this.power_bias_scale = .5;

    }

    setSimulationState(result){
        this.setState({
            isLoaded: true,
            updated: true,
            on: result.on,
            gain: result.gain,
            speed: result.speed,
            power_bias: result.power_bias,
            checkedSimulator: result.on,
            slider_gain: Math.round(result.gain * this.gain_scale ),
            slider_speed: Math.round(result.speed * this.speed_scale),
            slider_power_bias: Math.round(result.power_bias * this.power_bias_scale/10),
        });
    }

    getSimulation(){
        fetch(this.url)
            .then(response => response.json())
            .then(
                (result) => {
                    this.setSimulationState(result);
                },
                // Note: it's important to handle errors here
                // instead of a catch() block so that we don't swallow
                // exceptions from actual bugs in components.
                (error) => {
                    this.setState({
                        isLoaded: true,
                        updated: false,
                        error
                    });

                }
            );
    }

    postSimulation(){
        if(this.can_update) {
            this.can_update = false;
            setTimeout(() => {
                // Your code here
                let on = 0;
                if(this.state.checkedSimulator){
                    on = 2;
                }
                fetch(this.url, {
                    method: 'post',
                    headers: {
                        'Accept': 'application/json, text/plain, */*',
                        'Content-Type': 'application/json'
                    },
                    body: JSON.stringify({
                        on: on,
                        gain: this.state.gain,
                        speed: this.state.speed,
                        power_bias: this.state.power_bias,
                    })
                }).then(result => result.json())
                    .then(result => {
                            this.setSimulationState(result);
                            this.can_update = true;
                        },
                    );

            }, 1000);
        }
    }

    componentDidMount() {
        this.getSimulation();
    }

    handleSpeedSliderChange = (event, slider_speed) => {
        let speed = (slider_speed / this.speed_scale).toFixed(1);
        this.setState({ slider_speed, speed });
        this.postSimulation();

    };

    handlePowerBiasSliderChange =(event, slider_power_bias) => {
        slider_power_bias -= 50;
        let power_bias = (slider_power_bias / this.power_bias_scale * 10).toFixed(0);
        this.setState({ slider_power_bias, power_bias});
        this.postSimulation();

    };
    handleGainSliderChange = (event, slider_gain) => {
        let gain = ((slider_gain)/ this.gain_scale).toFixed(0);
        this.setState({slider_gain, gain});
        this.postSimulation();
    };

    handleChange = name => event => {
        this.setState({ [name]: event.target.checked });
        this.postSimulation()
    };

    render() {
        const {slider_speed, slider_gain, slider_power_bias} = this.state;
        let message;
        let simulator_control = <FormGroup row>
            <FormControlLabel
                control={
                    <Switch
                        checked={this.state.checkedSimulator}
                        onChange={this.handleChange('checkedSimulator')}
                        value="checkedSimulator"
                        color="primary"
                    />
                }
                label="Simulator On"
            />
        </FormGroup>;


        if(this.state.updated){
            if(this.state.on) {
                message = <div>
                    <br/><br/>
                    {simulator_control}
                    <br/><br/>
                    <h3>Speed: {(slider_speed / this.speed_scale).toFixed(1)} kts</h3>
                    <Slider
                        value={slider_speed}
                        aria-labelledby="set speed"
                        onChange={this.handleSpeedSliderChange}
                    />


                    <br/><br/>
                    <h3>Gain: {Math.round( slider_gain / this.gain_scale)}</h3>
                    <Slider
                        value={slider_gain}
                        aria-labelledby="set kd"
                        onChange={this.handleGainSliderChange}
                    />

                    <br/><br/>
                    <h3>Applied force: {(slider_power_bias / this.power_bias_scale).toFixed(0)}% max drive</h3>
                    <Slider
                        value={slider_power_bias + 50}
                        aria-labelledby="set power bias"
                        onChange={this.handlePowerBiasSliderChange}
                    />
                    <br/><br/>

                </div>
            }else{
                message =<div>
                    <br/><br/>
                        {simulator_control}
                        <br/><br/>
                </div>
            }
        }else{
            message = <paper><h2>No Communication: {this.url}</h2></paper>
        }
    return (message);
  }
}

export default Simulator;
