import React, { Component } from 'react';
import "./css/App.css";
import "what-input";
import Helm from './Helm';
import Config from './Config';
import Simulator from './Simulator';
import Toolbar from '@material-ui/core/Toolbar';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import CssBaseline from '@material-ui/core/CssBaseline';
import Grid from "@material-ui/core/Grid";
import Paper from '@material-ui/core/Paper';
import { withStyles } from '@material-ui/core/styles';


function TabContainer(props) {
    return (
        <Typography component="div" style={{ padding: 8 * 3 }}>
            {props.children}
        </Typography>
    );
}


const styles = theme => ({
    root: {
        flexGrow: 1,
        backgroundColor: theme.palette.background.paper,
    },
    course:{
        padding: theme.spacing.unit * 2,
        textAlign: 'center',
        color: theme.palette.text.secondary,
        height: 680,
        width: 410,
    },
    paper: {
        padding: theme.spacing.unit * 2,
        textAlign: 'center',
        color: theme.palette.text.secondary,
    },
});


class App  extends Component {

    constructor(props) {
        super(props);
        this.state = {
            value: 0,
        };
    }

    handleChange = (event, value) => {
        this.setState({ value });
    };


    render() {
        const { classes } = this.props;
        const { value } = this.state;
        return (
            <div>
                <CssBaseline />
                <AppBar position="static">
                    <Toolbar>
                        Navigation Dashboard  NavDash v0.5
                    </Toolbar>

                    <Tabs value={value} onChange={this.handleChange}>
                        <Tab label="Dash Board" />
                        <Tab label="Auto-helm Set Up" />
                        <Tab label="Auto-helm Simulator" />
                    </Tabs>
                </AppBar>
                {value === 0 && <TabContainer>
                                <Grid container className={classes.root} spacing={24}>
                                    <Grid item>
                                        <Paper  className={classes.course}><Helm /></Paper>
                                    </Grid>
                                </Grid>
                                </TabContainer>}
                {value === 1 && <TabContainer>
                                <Grid container className={classes.root} spacing={24}>
                                    <Grid item>
                                        <Paper className={classes.course}>
                                            <Helm/>
                                        </Paper>
                                    </Grid>

                                    <Grid item>
                                        <Paper className={classes.paper} >
                                        <h2>Configuration</h2>
                                        <Config />
                                        </Paper>
                                    </Grid>

                                    <Grid item>
                                        <Paper className={classes.paper}>
                                        <h2>Simulation</h2>
                                       <Simulator />
                                        </Paper>
                                    </Grid>
                                </Grid>
                            </TabContainer>}
                {value === 2 && <TabContainer><Simulator /></TabContainer>}
            </div>
        );
    }
}


export default withStyles(styles)(App);
