import React, { Component } from 'react';
import Slider from '@material-ui/lab/Slider';
import SteeringCompass from './Compass';
import Button from '@material-ui/core/Button';


class Helm extends Component {
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            isLoaded: false,
            updated: false,
            cts: 0,
            heading: 0,
            rose: 0,
            cts_slider_value: 50,
            trim_slider_value: 50,
        };

        this.url = process.env.REACT_APP_THIS_HOST + ":8079/api/orientation";
        this.set_cts = 0;
        this.local_control = 0;
        this.local_control_count_down = 3;
    }

    getCompass(){
        fetch(this.url)
            .then(response => response.json())
            .then(
                (result) => {
                    this.setState({
                        isLoaded: true,
                        updated: true,
                        cts: result.cts,
                        heading: result.heading,
                        pitch: result.pitch,
                        roll: result.roll,
                        calibration: result.calibration,
                    });
                },
                // Note: it's important to handle errors here
                // instead of a catch() block so that we don't swallow
                // exceptions from actual bugs in components.
                (error) => {
                    this.setState({
                        isLoaded: true,
                        updated: false,
                        error
                    });

                }
            );
    }

    postCts(){
        fetch(this.url, {
            method: 'post',
            headers: {
                'Accept': 'application/json, text/plain, */*',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                    "cts": this.set_cts
                })
        }).then(result=>result.json())
            .then(result => {
                    this.local_control = this.local_control_count_down;
                    this.setState({
                        isLoaded: true,
                        updated: true,
                        cts: result.cts,
                        heading: result.heading,
                    });
                },
            );

    }

    componentDidMount() {
        this.timer = setInterval(() => {
            this.getCompass();
            if(this.set_cts !== this.state.cts){
                if(this.local_control > 0) {
                    this.local_control--;
                }else {
                    this.setCTS(this.state.cts);
                }
            }
        }, 250);
    }

    componentWillUnmount(){
        clearInterval(this.timer);
    }

    conditionSetCTS(value){
        if(value < 0){
            value = 360 + value;
        }
        if (value > 360){
            value -= 360
        }
        this.set_cts = Math.round(value);
    }

    setCTS(value){
        this.conditionSetCTS(value);
        let  cts_slider_value = Math.round(this.set_cts/3.6 + 50);
        if(cts_slider_value < 0){
            cts_slider_value = 100 + cts_slider_value;
        }
        if(cts_slider_value > 100){
            cts_slider_value -= 100;
        }
        this.setState({ cts_slider_value });
    }

    handleCTSSliderChange = (event, cts_slider_value) => {
        this.setState({ cts_slider_value });
        this.conditionSetCTS((cts_slider_value-50) * 3.6);
        this.postCts();
    };

    handleTrimSliderChange = (event, trim_slider_value) => {
        this.setState({ trim_slider_value });
        let trim = (trim_slider_value - 50) /10 + this.state.cts_slider_value - 50;
        this.conditionSetCTS(trim * 3.6);
        this.postCts();
    };

    render() {
        const { cts_slider_value, cts,  trim_slider_value} = this.state;
        let { heading } = this.state;
        heading = Math.round(heading);
        let compass;
        if(this.state.updated){
            compass =  <SteeringCompass cts={this.set_cts} heading={this.state.heading} />
        }else{
            compass = <paper><h2>No Communication: {this.url}</h2></paper>;
        }
    return (
                <div>
                    <div className="pad">
                        {compass}
                    </div>
                    <div className="pad">
                         <h3>Heading <b>{heading}</b> M &nbsp;&nbsp; &nbsp;CTS <b>{cts}</b> M</h3>
                        <h4>Course to Steer: </h4>
                        <div>
                            <span className="floatLeft">
                                <Button  color="primary" onClick={
                                ()=>{
                                    this.setCTS( this.set_cts - 85);
                                    this.postCts();
                                }}>-85</Button>
                                <Button   color="primary" onClick={
                                ()=>{
                                    this.setCTS( this.set_cts - 5);
                                    this.postCts();
                                }}>-5</Button>
                            </span>
                            <span className="floatRight">
                                <Button color="primary" onClick={
                                ()=>{
                                    this.setCTS( this.set_cts + 5);
                                    this.postCts();
                                }}>+5</Button>

                                <Button color="primary" onClick={
                                ()=>{
                                    this.setCTS( this.set_cts + 85);
                                    this.postCts();
                                }}>+85</Button>
                            </span>
                            <Slider
                                value={cts_slider_value}
                                aria-labelledby="set cts"
                                onChange={this.handleCTSSliderChange}
                            />

                        </div>
                    <br /> <br />
                    <h4>Trim Course:</h4>
                        <Slider
                            value={trim_slider_value}
                            aria-labelledby="trim course"
                            onChange={this.handleTrimSliderChange}
                        />
                        <br/>
                        <Button size="small" color="primary"  onClick={
                            ()=>{
                                this.local_control = this.local_control_count_down;
                                let adjust = (trim_slider_value - 50)  * 0.36;
                                this.setCTS(this.set_cts-adjust);
                                this.state.trim_slider_value = 50;
                                this.setState({ trim_slider_value: 50 });
                            }} >
                            Keep
                        </Button>

                        <Button size="small" color="primary" onClick={
                            ()=>{
                                this.conditionSetCTS((cts_slider_value-50) * 3.6);
                                this.postCts();
                                this.state.trim_slider_value = 50;
                                this.setState({ trim_slider_value: 50 });
                            }}>Return</Button>
                    </div>
                </div>

    );
  }
}

export default Helm;
