import React, { Component } from 'react';
import Slider from '@material-ui/lab/Slider';
import Paper from '@material-ui/core/Paper';
import Button from '@material-ui/core/Button';


class Config extends Component {
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            isLoaded: false,
            updated: false,
            kp: 0,
            kd: 0,
            ki: 0,
            gain: 0,
            rudder_rate: 0,
            set_cal: 0,
            slider_kp: 0,
            slider_ki: 0,
            slider_kd: 0,
            slider_gain: 0,
            slider_rudder_rate: 0,
            gain_slider_multiplier: 12,
        };

        this.url = process.env.REACT_APP_THIS_HOST + ":8079/api/calibration";
        this.can_update = true;
        this.rudder_rate_scale = 50;
    }

    setCalibrationState(result){
        let kp = result.kp;
        let kd = result.kd;
        let ki = result.ki;
        let max_value = kp + kd + ki;
        max_value= Math.max(max_value, 15);
        let gain = 100/ max_value;

        console.log(max_value, gain);
        this.setState({
            isLoaded: true,
            updated: true,
            kp: kp,
            kd: kd,
            ki: ki,
            rudder_rate: result.rudder_rate,
            max_value: max_value,
            slider_kp: Math.round(kp * gain),
            slider_kd: Math.round(kd * gain),
            slider_ki: Math.round(ki * gain),
            slider_gain: Math.round(max_value/this.state.gain_slider_multiplier),
            slider_rudder_rate: Math.round(result.rudder_rate * this.rudder_rate_scale),
            set_cal: result.set_cal,
            calibration: result.calibration,
        });
    }

    getCalibration(){
        fetch(this.url)
            .then(response => response.json())
            .then(
                (result) => {
                    this.setCalibrationState(result);
                },
                // Note: it's important to handle errors here
                // instead of a catch() block so that we don't swallow
                // exceptions from actual bugs in components.
                (error) => {
                    this.setState({
                        isLoaded: true,
                        updated: false,
                        error
                    });

                }
            );
    }

    postCalibration(){
        if(this.can_update) {
            this.can_update = false;
            setTimeout(() => {
                // Your code here

                fetch(this.url, {
                    method: 'post',
                    headers: {
                        'Accept': 'application/json, text/plain, */*',
                        'Content-Type': 'application/json'
                    },
                    body: JSON.stringify({
                        "kp": this.state.slider_kp * this.state.max_value / 100,
                        "kd": this.state.slider_kd * this.state.max_value / 100,
                        "ki": this.state.slider_ki * this.state.max_value / 100,
                        "rudder_rate": this.state.rudder_rate,
                        "set_cal": this.set_cal,
                    })
                }).then(result => result.json())
                    .then(result => {
                            this.setCalibrationState(result);
                            this.can_update = true;
                        },
                    );

            }, 1000);
        }
    }

    componentDidMount() {
        this.getCalibration();
    }

    static relativePerCent(principle, a, b){
        a += 0.5;
        b += 0.5;
        let factor = (100 - principle)/(a + b);
        principle = Math.round(principle);
        a = Math.round(a * factor);
        b = Math.round(b * factor);
        return [principle, a, b];
    }

    handleKpSliderChange = (event, slider_kp) => {
        let result = Config.relativePerCent(slider_kp, this.state.slider_kd, this.state.slider_ki);
        slider_kp = result[0];
        let slider_kd = result[1];
        let slider_ki = result[2];
        this.setState({ slider_kp, slider_kd, slider_ki});
        this.postCalibration();

    };

    handleKdSliderChange = (event, slider_kd) => {
        let result = Config.relativePerCent(slider_kd, this.state.slider_kp, this.state.slider_ki);
        slider_kd = result[0];
        let slider_kp = result[1];
        let slider_ki = result[2];
        this.setState({ slider_kp, slider_kd, slider_ki});
        this.postCalibration();
    };

    handleKiSliderChange = (event, slider_ki) => {
        let result = Config.relativePerCent(slider_ki, this.state.slider_kp, this.state.slider_kd);
        slider_ki = result[0];
        let slider_kp = result[1];
        let slider_kd = result[2];
        this.setState({ slider_kp, slider_kd, slider_ki});
        this.postCalibration();
    };

    handleGainSliderChange = (event, slider_gain) => {
        slider_gain = Math.round(slider_gain);
        slider_gain = Math.max(slider_gain, 5);
        let max_value = slider_gain * this.state.gain_slider_multiplier ;
        this.setState({slider_gain, max_value});
        this.postCalibration();
    };

    handleRudderRateSliderChange= (event, slider_rudder_rate) => {
        let rudder_rate = (slider_rudder_rate / this.rudder_rate_scale).toFixed(2);
        this.setState ( {slider_rudder_rate, rudder_rate});
        this.postCalibration();
    };

    render() {
        const {slider_kp, slider_kd, slider_ki, slider_gain, slider_rudder_rate} = this.state;
        let message;
        if(this.state.updated){
            message =

                <div>
                    <br /><br />
                <h3>Relative Kp: {slider_kp}%</h3>
                <Slider
                value={slider_kp}
                aria-labelledby="set kp"
                onChange={this.handleKpSliderChange}
                />

                <br /><br />
                <h3>Relative Kd: {slider_kd}%</h3>
                <Slider
                value={slider_kd}
                aria-labelledby="set kd"
                onChange={this.handleKdSliderChange}
                />

                <br /><br />
                <h3>Relative Ki: {slider_ki}%</h3>
                <Slider
                value={slider_ki}
                aria-labelledby="set ki"
                onChange={this.handleKiSliderChange}
                />

                <br /><br />
                <h3>K Value at 100%: {slider_gain * this.state.gain_slider_multiplier}</h3>
                <Slider
                value={slider_gain}
                aria-labelledby="set ki"
                onChange={this.handleGainSliderChange}
                />
                <br /><br />
                <h3>Drive Rudder Rate: {(slider_rudder_rate / this.rudder_rate_scale).toFixed(2)} degrees per second</h3>
                    <Slider
                        value={slider_rudder_rate}
                        aria-labelledby="set rudder rate"
                        onChange={this.handleRudderRateSliderChange}
                    />

                <br /><br /><br />
                <Paper>
                <h3>Compass/Orientation Chip Calibration:</h3>
                <Button variant="contained" size="small" color="primary"  onClick={
                ()=>{

                }}>Erase</Button>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                <Button variant="contained" size="small" color="primary" onClick={
                ()=>{

                }}>Store</Button>
                <br /><br /><br />
                </Paper>
                </div>

        }else{
            message = <paper><h2>No Communication: {this.url}</h2></paper>
        }
        return(message);
  }
}

export default Config;
